:root {
    --standart-indent: 16px;
    --whiteColor: #fff;
}
.statistics {
    /* // */
    margin-top: 30px;
    text-align: center;
    width: calc(var(--standart-indent) * 19);
    margin-bottom: 5px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.title {
    margin: 0;

    font-size: calc(var(--standart-indent) * 2);
    line-height: 2;
}

.statList {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none;
    /* // */
}

.item {
    height: calc(var(--standart-indent) * 4);
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: calc(var(--standart-indent) / 4);
    color: var(--whiteColor);
    /* // */
}

.label {
    /* // */
    display: block;
    font-size: calc(var(--standart-indent) / 1.25);
    margin-bottom: calc(var(--standart-indent) / 2);
}

.percentage {
    /* // */
    display: block;
    font-size: calc(var(--standart-indent));
}
